import { Component, h, Prop, Host, FunctionalComponent, Element } from '@stencil/core';
import AccountIcon from '@material-design-icons/svg/outlined/account_circle.svg';
import LocationIcon from '@material-design-icons/svg/outlined/location_on.svg';
import StarIcon from '@material-design-icons/svg/outlined/star.svg';
import { UserCardIconType } from '../../../../interfaces/components/basic/cards/user-card/user-card';

@Component({
  tag: 'rwc-user-card',
  styleUrl: 'user-card.scss',
  shadow: true
})
export class RwcUserCard {
  /**
   * The reference to the host element.
   */
  @Element() host: HTMLElement;

  /**
   * Path to the icon.
   */
  @Prop({ reflect: true }) avatar: string = '';

  /**
   * Set avatar rounded.
   */
  @Prop() roundedAvatar: boolean = false;

  /**
   * Set rendering of box-shadow.
   */
  @Prop({ reflect: true }) renderShadow: boolean = true;

  /**
   * Set rendering of border.
   */
  @Prop({ reflect: true }) renderBorder: boolean = false;

  /**
   * Animate border on hover.
   */
  @Prop({ reflect: true }) animateBorder: boolean = false;

  /**
   * Path to the icon.
   */
  @Prop({ reflect: true }) locationIcon: string = '';

  /**
   * Path to the icon.
   */
  @Prop({ reflect: true }) ratingIcon: string = '';

  /**
   * Change cursor to pointer on hover.
   */
  @Prop() pointer: boolean = false;

  /**
   * Check if slot location is rendered.
   */
  private get hasLocation(): boolean {
    return Boolean(this.host.querySelector('[slot="location"]'));
  }

  /**
   * Check if slot rating is rendered.
   */
  private get hasRating(): boolean {
    return Boolean(this.host.querySelector('[slot="rating"]'));
  }

  /**
   * Renders the component.
   */
  render(): FunctionalComponent {
    return (
      <Host>
        <rwc-card
          class="card"
          renderShadow={ this.renderShadow }
          renderBorder={ this.renderBorder }
          animateBorder={ this.animateBorder }
          pointer={ this.pointer }
        >
          <div class="container">
            <div class="header">
              <slot name="header"></slot>
            </div>
            <div class="avatar">
              <slot name="avatar">
                { this.renderIcon('avatar') }
              </slot>
            </div>
            <div class="name content-spacing">
              <slot name="name"></slot>
            </div>
            <div class="heading content-spacing">
              <slot name="heading"></slot>
            </div>
            { this.renderLocation() }
            { this.renderRating() }
            <div class="description">
              <slot name="description">
              </slot>
            </div>
          </div>
        </rwc-card>
      </Host>
    );
  }

  /**
   * Renders location element
   */
  renderLocation(): FunctionalComponent {
    if (this.hasLocation) {
      return (
        <div class="location-icon content-spacing">
          <span>{ this.renderIcon('location') }</span>
          <span>
            <slot name="location"></slot>
          </span>
        </div>
      )
    }
  }

  /**
   * Renders rating element.
   */
  renderRating(): FunctionalComponent {
    if (this.hasRating) {
      return (
        <div class="rating-icon content-spacing">
          <span>{ this.renderIcon('rating') }</span>
          <span>
            <slot name="rating"></slot>
          </span>
        </div>
      )
    }
  }

  /**
   * Renders the icon element.
   */
  renderIcon(icon: UserCardIconType): FunctionalComponent {
    switch (icon) {
      case 'location':
        if (this.locationIcon) {
          return (
            <img src={ this.locationIcon } alt="location-icon"></img>
          )
        }
        return (
          <div innerHTML={ LocationIcon } class="svg-icon"></div>
        );
      case 'rating':
        if (this.ratingIcon) {
          return (
            <img src={ this.ratingIcon } alt="rating-icon"></img>
          )
        }
        return (
          <div innerHTML={ StarIcon } class="svg-icon"></div>
        )
      default:
        if (this.avatar) {
          return (
            <img src={ this.avatar } alt="avatar" class={ { rounded: this.roundedAvatar } }></img>
          )
        }
        return (
          <div innerHTML={ AccountIcon } class="svg-icon"></div>
        )
    }
  }
}
