@import 'global.scss';

:host {
  /**
  * @prop --user-card-border-width: The border width of the user card.
  * @prop --user-card-border-style: The style of the border of the user card.
  * @prop --user-card-border-color: The color of the border of the user card.
  * @prop --user-card-hover-border-color: The color of the border of the card on hover.
  * @prop --user-card-border-radius: The border radius of the user card.
  * @prop --user-card-border-transition: The transition of the border of the user card.
  * @prop --user-card-box-shadow: The shadow of the user card.
  * @prop --user-card-box-shadow-animated-hover: The animation of the box shadow of the user card on hover.
  * @prop --user-card-container-transition: The transition of the container of the user card.
  * @prop --user-card-inner-padding: The inner padding of the user card.
  * @prop --user-card-icon-size: The size of the icon of the user card.
  * @prop --user-card-icon-color: The color of the icon of the user card.
  * @prop --user-card-content-spacing: The spacing between contents of the user card.
  * @prop --user-card-icon-sm-size: The size of the small icon of the user card.
  * @prop --user-card-icon-sm-color: The color of the small icon of the user card.
  * @prop --user-card-font-size-header: The size of the small font of the user card.
  * @prop --user-card-font-size-name: The size of the medium font of the user card.
  * @prop --user-card-font-size-heading: The size of the large font of the user card.
  * @prop --user-card-font-weight-bold: The weight of the font of the user card.
  * @prop --user-card-font-weight-extra-bold: The weight of the font of the user card.
  * @prop --user-card-icon-sm-margin-right: The right margin of the small icon of the user card.
  * @prop display: The display mode of the host element.
  */
  --user-card-border-width: 2px;
  --user-card-border-style: solid;
  --user-card-border-color: #{$rwc-color-border-primary};
  --user-card-hover-border-color: #{$rwc-color-border-active};
  --user-card-border-radius: #{$rwc-spacing--sm};
  --user-card-border-transition: 
    border-bottom-color 0.4s, 
    border-left-color 0.4s, 
    border-right-color 0.4s, 
    border-top-color 0.4s, 
    width 0.4s ease-in-out, 
    height 0.4s ease-in-out;
  --user-card-box-shadow: #{$rwc-box-shadow-lg};
  --user-card-box-shadow-animated-hover: #{$rwc-box-shadow-xl};
  --user-card-container-transition: box-shadow 0.4s ease-in-out;

  --user-card-content-spacing: #{$rwc-spacing--xs};
  --user-card-description-margin-top: #{$rwc-spacing--md};

  --user-card-font-size-header: #{$rwc-font-size-heading-sm};
  --user-card-font-size-name: #{$rwc-font-size-body-lg};
  --user-card-font-size-heading: #{$rwc-font-size-heading-md};
  --user-card-font-weight-bold: 500;
  --user-card-font-weight-extra-bold: 600;

  --user-card-inner-padding: #{$rwc-spacing--md};

  --user-card-icon-size: 5.625rem;
  --user-card-icon-sm-size: 1.406rem;
  --user-card-icon-color: #{$rwc-color-grey-600};
  --user-card-icon-sm-color: #{$rwc-color-grey-900};
  --user-card-icon-sm-margin-right: #{$rwc-spacing--2xs};

  display: block;
}

.card {
  --card-border-width: var(--user-card-border-width);
  --card-border-style: var(--user-card-border-style);
  --card-border-color: var(--user-card-border-color);
  --card-hover-border-color: var(--user-card-hover-border-color);
  --card-border-radius: var(--user-card-border-radius);
  --card-border-transition: var(--user-card-border-transition);
  --card-box-shadow-animated-hover: var(--user-card-box-shadow-animated-hover);
  --card-container-transition: var(--user-card-container-transition);
  --card-inner-padding: var(--user-card-inner-padding);
  --card-box-shadow: var(--user-card-box-shadow);
}

.container {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.header {
  display: flex;
  justify-content: flex-end;
  font-size: var(--user-card-font-size-header);
  font-weight: var(--user-card-font-weight-extra-bold);
}

.name {
  font-size: var(--user-card-font-size-name);
}

.heading {
  font-size: var(--user-card-font-size-heading);
  font-weight: var(--user-card-font-weight-bold);
}

.description {
  font-size: #{$rwc-font-size-body-sm};
  margin-top: var(--user-card-description-margin-top);
}

.content-spacing {
  margin-top: var(--user-card-content-spacing);
}

.avatar {
  align-self: center;
  flex-shrink: 0;
  height: var(--user-card-icon-size);
  width: var(--user-card-icon-size);

  svg {
    height: 100%;
    width: 100%;
  }

  path {
    fill: var(--user-card-icon-color);
  }

  img {
    width: 100%;
    height: 100%;
  }

  img.rounded {
    border-radius: 50%;
  }
}

.location-icon, .rating-icon  {
  display: flex;
  justify-content: center;

  svg {
    height: 100%;
    width: 100%;
  }

  .svg-icon {
    margin-right: var(--user-card-icon-sm-margin-right);
  }

  path {
    fill: var(--user-card-icon-sm-color);
  }

  img {
    width: var(--user-card-icon-sm-size);
    height: var(--user-card-icon-sm-size);
  }
}
